  font-size: 15px;
  color: #fff;
  font-weight: bold;
  text-shadow: 0 0 4px #fff;
  letter-spacing: 1px;
  float: right;
}

a.nav-link.about-us {
  margin-right: 20px;
}
.content-bg {
  background: rgba(39, 49, 61, 0.7);
  padding: 15px 15px 35px 15px;
  margin: 40px 0px 20px 0px;
  border-radius: 5px;
}
h1.inner-page-heading {
  color: #fff;
  margin: 0px 0 20px 0;
}
.col-12.inner-content {
  font-family: sans-serif;
  line-height: 32px;
  color: #fff;
}
.backgroundContainer {
  background: url(/img/body-bg.jpg);
  background-size: cover;
  height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
}
.currentLocation {
  cursor: pointer;
}
.location-dark-container {
  background: #27293d;
  border-radius: 5px;
  padding: 15px 15px 0px;
}
.location-bg {
  background: #fff;
  display: flex;
  border-radius: 25px;
  align-items: center;
  position: relative;
}

h5.nearby {
  color: #fff;
  margin-top: 20px;
  text-shadow: 0 0 2px #808080;
}
section.profile-page input.form-control.fieldError {
  border: 2px solid #ff2a46 !important;
}
span.nightlife {
  display: none;
}
.nightlife.desktop-text {
  display: inline-block;
  float: none;
  margin-left: 15px;
  position: relative;
  top: 1px;
  font-size: 17px;
}
.nav-item a.nav-link.about-us {
  margin-right: 25px !important;
  margin-left: 16px;
}
.display-block.lounge-box.pb-4 {
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
}
.buttonCursor {
  cursor: none;
}
.modal-content.noDj-play .modal-body {
  padding-top: 0;
  padding-bottom: 0;
}
.modal-content.noDj-play button.close.text-right {
  position: absolute;
  right: 25px;
  top: -7px;
  bottom: 0;
}

.modal-content.noDj-play h5 {
  margin-bottom: 0px;
}
p.address-section a:hover {
  text-decoration: none;
  color: #fff;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
p.address-section a {
  color: #50c7ff;
}
p.address-section .fa {
  color: #50c7ff;
}
p.address-section b {
  font-weight: 500;
}
.abouts-footer100vh {
  margin-top: 158px;
}
span.red-color,
span.green-color {
  background: radial-gradient(red, #8a0000);
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 2px solid #fff;
  box-shadow: 0 0 3px #000;
  position: absolute;
  right: 5px;
  top: 5px;
}

span.green-color {
  background: radial-gradient(#00ce00, #006500);
}

span.recent-clubs-img::after {
  width: 100%;
  position: absolute;
  height: 100%;
  content: "";
  background: #000;
  opacity: 0.5;
  left: 0;
  top: 0;
}
.search-dropdown {
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 12px 16px;
  z-index: 1;
  margin-top: 45px;
  margin-left: 5px;
  width: 90%;
  list-style-type: none;
}
.city-search .search-dropdown {
  width: 79%;
  z-index: 3;
  padding: 0;
  top: 6px;
  right: 20px;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.3);
}
.city-search .search-dropdown li,
.display-block.type-something ul li {
  border-bottom: 1px solid #cacaca;
  padding: 15px;
  background: #e8e8e8;
}
.city-search .search-dropdown li:last-child,
.display-block.type-something ul li:last-child {
  border-bottom: none;
}
.city-search .search-dropdown li:hover,
.display-block.type-something ul li:hover {
  background: #f8556b;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
}
.city-search .search-dropdown li:last-child:hover,
.display-block.type-something ul li:last-child:hover {
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
}
.city-search .search-dropdown li:hover,
.display-block.type-something ul li:hover {
  color: #fff;
}
.city-search .has-search .form-control {
  padding-left: 0;
}
.city-search .has-search .form-control:focus {
  box-shadow: none;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.modal {
  z-index: 10 !important;
}

.display-block.type-something {
  position: relative;
  border-radius: 5px;
}
.display-block.type-something ul.search-dropdown {
  top: 52px;
  margin: 0;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  width: 100%;
  padding: 0;
  overflow: hidden;
  box-shadow: 0 1px 3px rgba(156, 156, 156, 0.2);
  border: 1px solid #cacaca;
}
.search-dropdown li.no-match:hover {
  background: linear-gradient(45deg, #e8e8e8, #eaeaea00) !important;
  color: #495057 !important;
  cursor: none !important;
}
.d-block.another-city {
  margin-top: 25px;
}
.d-block.another-city ul {
  padding: 0;
  margin: 0;
}
.d-block.another-city ul li {
  border-bottom: 1px solid #9a9a9a;
  display: inherit;
  padding: 13px 0 5px 0;
  width: 46%;
  float: left;
  font-size: 14px;
  cursor: pointer;
  padding-left: 8px;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.d-block.another-city ul li:hover {
  /*background: #f8556b;*/
  color: #f8556b;
  cursor: pointer;
  -webkit-transition: all 0.3s ease-in-out;
  transition: all 0.3s ease-in-out;
}
.d-block.another-city ul li.active-city {
  background: #f8556b;
  color: #fff;
}

.d-block.another-city ul li:nth-child(even) {
  margin-left: 8%;
}
.modal-line {
  margin-top: 40px !important;
}
span.red-color-list,
span.green-color-list {
  background: radial-gradient(red, #8a0000);
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 2px solid #fff;
  box-shadow: 0 0 3px #000;
  position: absolute;
  right: 30px;
  top: 30px;
}

span.green-color-list {
  background: radial-gradient(#00ce00, #006500);
}
.radio-error {
  border: 3px solid #ff0022;
}
.css-df17o1 {
  color: #fff;
  background: rgba(0, 0, 0, 0.9) !important;
}

